<template>
    <form @submit.prevent="AddEgreso" >
              <div>

                <select 
                        name="fuente" 
                        id="disabledSelect"
                        class="form-select"
                        
                        v-model="egreso.fuente"
                    >
                    <option value="0" disabled selected hidden >Seleccione una fuente</option>
                    <option value="NEQUI">NEQUI</option>
                    <option value="BANCOLOMBIA">BANCOLOMBIA</option>
                    <option value="EFECTIVO">EFECTIVO</option>
                    </select>
                               
                   <input
                      type='date'
                      name='fecha'
                      class='form-control mt-3'
                      placeholder='Fecha'
                      autoComplete='off'
                      v-model="egreso.fecha"
                  /> 
              
                  
                  <input
                      type='text'
                      name='descripcion'
                      class='form-control mt-3'
                      placeholder='Descripcion'
                      autoComplete='off'
                      v-model="egreso.descripcion"
  
                  />
                   <input
                      type='number'
                      name='monto'
                      class='form-control mt-3'
                      placeholder='Monto'
                      autoComplete='off'
                      v-model="egreso.monto"
  
                  />
                  
  
                  <button 
                  class='btn btn-primary form-control  mt-2'
                  type='submit'  
                  :disabled="getIsLoadingFerney"
                  >
                  Guardar
  
                  </button>
  
              </div>
          </form>
  </template>
  
  <script>
  import { mapActions, mapGetters } from 'vuex'
  
  export default {
      data() {
          return{
              egreso:{}
          }
      },
      computed:{
          ...mapGetters('ferneyEgreso',['getIsLoadingFerney', 'getFuentesEgreso'])
      },
      methods:{
          ...mapActions('ferneyEgreso',['addEgresoFerney']),
          async AddEgreso(){

            if(this.egreso.fuente == 0){
                Swal.fire({
                    position:'center',
                    title:'Selecciona una fuente',
                })
            }else{
              var egresoAdd = {
                  fecha: this.egreso.fecha,
                  descripcion: this.egreso.descripcion,
                  monto: this.egreso.monto,
                  fuente: this.egreso.fuente
              }
              await this.addEgresoFerney(egresoAdd)
            }
          }
      },
      created(){
        this.egreso.fuente = 0
      }
  }
  </script>
  
  <style>
  
  </style>